.currentWeather,
.hourlyWeather {
  margin: 0 auto;
  max-width: 350px;
}
.dailyWeather {
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-content: space-around;
  justify-content: space-around;
}

@media (min-width: 760px) {
  .weather {
    display: flex;
    flex-wrap: wrap;
    align-content: space-around;
    justify-content: space-around;
  }
  .currentWeather,
  .hourlyWeather {
    flex-basis: 40%;
  }
}

@media (min-width: 1300px) {
  .weather {
    margin: 0 auto;
    width: 98%;
    justify-content: space-between;
  }
  .currentWeather,
  .hourlyWeather {
    margin: 0;
    flex-basis: 20%;
  }
}
