.weatherContainer {
  margin: 10px auto;
  width: 95%;
  border: 1px solid #dbdbdb;
  border-radius: 10px;
}
.weatherDescription {
  font-size: 0.8rem;
  text-align: center;
}
.weatherContainer img {
  display: block;
  margin: 0 auto;
}

.weatherContainer div {
  display: flex;
  justify-content: space-between;
  padding: 4px;
  border-top: 1px solid #dbdbdb;
}
