.weatherForOneDayContainer {
  font-size: 0.8rem;
  margin: 0 auto;
  width: 48%;
}

@media (min-width: 760px) {
  .weatherForOneDayContainer {
    font-size: 0.8rem;
    margin: 0 auto;
    width: 33%;
  }
}
@media (min-width: 1020px) {
  .weatherForOneDayContainer {
    width: 33%;
  }
}

@media (min-width: 1430px) {
  .weatherForOneDayContainer {
    width: 14.2%;
  }
}
