.finderContainer {
  margin: 0 auto;
  width: 90%;
  max-width: 500px;
}

@media (min-width: 1300px) {
  .finderContainer {
    position: absolute;
    top: 20%;
    left: 50%;
    width: 500px;
    transform: translateX(-50%);
  }
}
label {
  display: block;
  margin: 15px 0;
  text-align: center;
  width: 100%;
  font-size: 1.5rem;
}

.finder {
  display: flex;
  margin: 5px;
  text-align: center;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border: 2px solid #dbdbdb;
  border-radius: 15px;
}
.finder:hover {
  box-shadow: 0px 0px 5px 0px #dbdbdb;
}

.searchBar {
  display: flex;
  position: relative;
  width: 90%;
  margin: 0 10px;
  padding: 5px;
  justify-content: space-around;
  align-items: center;
}
input {
  padding: 10px;
  width: 85%;
  font-size: 1.5rem;
  font-weight: bold;
  height: 30px;
  text-align: center;
  border: none;
  background-color: transparent;
  color: white;
}
input:focus {
  outline: none;
}

.search {
  position: absolute;
  top: 10px;
  padding: 5px;
  right: 0;
  height: 30px;
  width: 30px;
  fill: #9aa0a6;
  border-radius: 12px;
  transition: 0.5s;
}
.searchBtn {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.search:hover {
  background-color: #768492;
  cursor: pointer;
}
