* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  min-height: 100vh;
  background: linear-gradient(#1b2632, #6d9bcd) fixed;
  color: white;
}
.wrapper {
  margin: 0 auto;
  max-width: 1320px;
}

h1 {
  margin: 10px 0;
  text-align: center;
}
h2 {
  margin: 10px 0;
  text-align: center;
  width: 100%;
}

h3 {
  margin: 10px 0;
  text-align: center;
  font-size: 1rem;
}
